<template>
    <div class="fr-component">
        <div id="msgPanel" :style="msgPanelStyle" class="contact" @click="showMsg()" >
            <i class="fa fa-comments" aria-hidden="true"></i>
            <div class="msg-text"> </div>
        </div>
    </div>
</template>

<script>
import '@/assets/floatPanel.min.js'

export default {
    data(){
        return {
            st:0,
            panelMsg:null,
            panelMsgBody:`
            <div>
                <form action="">
                    <div class="form-group">
                        <label for="">Email: </label>
                        <input type="email" class="form-control" id="" placeholder="Email">
                    </div>
                    <div class="form-group">
                        <label for="">: </label>
                        <input type="phone" class="form-control" id="" placeholder="Phone">
                    </div>
                    <div class="form-group">
                        <label for="">: </label>
                        <textarea class="form-control" id="" placeholder=""></textarea>
                    </div>
                    <button id="msg-submit" class="btn btn-primary btn-block" type=""></button>
                </form>
            </div>`,
        }
    },
    computed:{
        msgPanelStyle(){
            if(this.st>=0){
                return {
                    display:'block'
                }
            }
            return {
                display:'none',
            }
        }
    },
    methods:{
        handleScroll(){
            var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
            // console.log(scrollTop)
            this.st = scrollTop;
        },
        showMsg(){
            if(!this.panelMsg){
                this.createMsg()
            }
            this.panelMsg.reposition();
            this.panelMsg.show();
        },
        createMsg(){
            // console.log(11)
            this.panelMsg =  window.FloatPanel('panelMsg',{
                width:500,
                header:{
                    enabled:true,
                    content:''
                },
                body:{
                    enabled:true,
                    content:this.panelMsgBody,
                },
                footer:{
                    enabled:false,
                },
                closeBtn: true,
                backDrop: true,
            });

            var _this = this;(
            this.panelMsg._backDropNode.addEventListener('click',function(vm){
                return function(){
                    // console.log(_this.panelMsg)
                    vm.panelMsg.hide();
                }
            }(_this)));

            var btn = document.querySelector('#msg-submit');
            btn.addEventListener('click',this.sendMsg)
        },
        sendMsg(event){
            event.preventDefault();
            console.log(event);
            
        },
    },
    mounted(){
        window.addEventListener('scroll',this.handleScroll);
        this.$store.commit('setFrComponent',this);
    },
    beforeDestroy(){
        window.removeEventListener('scroll',this.handleScroll);
        // document.querySelector('msg-submit').removeEventListener('click',this.sendMsg)
    }
}
</script>
<style lang="less">
    @import url('../assets/floatPanel.min.css');
    #panelMsg{
        .panel-body{
            .form-control{
                margin-bottom: 10px;
            }
            input{
                padding: 5px;
            }
            textarea{
                padding: 5px;
                width: 100%;
                height: 100px;
            }
        }
        .btn{
            padding: 6px 12px;
        }
    }
</style>
<style lang="less" scoped>
    .fr-component{
        text-decoration: none;
        position: fixed;
        bottom: 20px;
        right: 2.2%;
        overflow: hidden;
        z-index: 999;
        width: auto;
        height: auto;
        overflow:visible;
        // border: 1px solid #ccc;
        .contact{
            width: 40px;
            height: 40px;
            border-radius: 40px;
            background-color: #198eee;
            border: 1px solid #fff;
            text-align: center;
            cursor: pointer;
            transition: .5s all; 
            opacity:.5;
            transform: scale(.9);
            i.fa{
                font-size:24px;
                color:#fff;
                line-height: 38px;
            }
            &:hover{
                opacity:1;
                transform:scale(1);
            }
        }
        .msg-text{
            color:rgb(20, 35, 82);
            background-color: rgba(255,255,255,.1);
        }
    }
    @media screen and (min-width:768px) {
        .fr-component{
            bottom: 40px;
        }
    }
</style>