import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    frComponent:null,
  },
  getters:{
    getFrComponent(state){
      return state.frComponent;
    },
  },
  mutations: {
    setFrComponent(state,payload){
      state.frComponent = payload;
    },
  },
  actions: {

  }
})
